.container3{display:flex;justify-content:center;align-items:center;text-align:center;padding:2rem;background-image:url("../../assets/picture/BG/背景-防空洞.png");background-size:cover;background-position:center;height:100vh;width:100vw;font-family:"Comic Sans MS",cursive,sans-serif}.content3{display:flex;flex-direction:column;align-items:center;max-width:90%;margin:0 auto;color:#fff;font-weight:bold}.content3 .text3{margin-bottom:1rem;font-size:2.5rem;color:#f5f5f5;text-shadow:2px 2px 4px rgba(0,0,0,.5)}.content3 .button3{padding:1.5em 3em;font-size:2rem;background-color:#8b5a2b;color:#fff;border:none;border-radius:15px;cursor:pointer;margin-top:2.5rem;transition:background-color .3s ease,transform .3s ease;box-shadow:0 4px 8px rgba(0,0,0,.2)}.content3 .button3:hover{background-color:#a07246;transform:scale(1.05)}.content3 .endButton3{margin-top:3rem}.content3 .finalButton3{margin-top:3rem}.content3 .input3{padding:1em;font-size:1.5rem;width:100%;max-width:400px;margin-bottom:1.25rem;border:1px solid #ccc;border-radius:5px;box-shadow:0 2px 4px rgba(0,0,0,.2)}@media screen and (max-width: 768px){.content3 .text3{font-size:1.5rem}.content3 .input3{font-size:2rem}.content3 .button3{font-size:1rem}}